<template>
    <div class="container-fluid pb-3">
        <div class="d-flex justify-content-between align-items-center">
            <h2 class="main-title mb-2">Federal Tender</h2>
            <router-link to="/federal_tenders" class="btn btn-primary mb-2">Federal Bids</router-link>
        </div>
        <div class="row g-3">
            <div class="col-md-12">
                <form class="card" @submit.prevent="submitForm()">
                    <div class="card-header">
                        <div class="d-sm-flex align-items-center justify-content-between">
                            <h6 class="card-title mb-0" v-if="status"><strong>Add Federal Bid</strong></h6>
                            <h6 class="card-title mb-0" v-else><strong>Update Federal Bid</strong></h6>
                        </div>
                    </div>
                    <div class="card-body">
                        <div class="row">
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label>Region <span class="text-danger">*</span></label>
                                    <select class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors?.region }" v-model="tender.region" disabled="true">
                                        <option value="state">State</option>
                                        <option value="federal">Federal</option>
                                        <option value="private">Private/Commercial</option>
                                        <option value="international">International</option>
                                    </select>
                                    <span v-if="errors?.region" class="invalid-feedback">{{ errors?.region[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label>Country <span class="text-danger">*</span></label>
                                    <search :class="{ 'is-invalid': errors?.country_id }"
                                        :customClass="{ 'is-invalid': errors?.country_id }"
                                        :initialize="tender.country_id" id="country_id" label="country_name"
                                        placeholder="Select Country" :data="countries"
                                        @input="country => handleCountryChange(country)">
                                    </search>
                                    <span v-if="errors?.country_id" class="invalid-feedback">{{ errors?.country_id[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label>State / Location Issued <span class="text-danger">*</span></label>
                                    <search :class="{ 'is-invalid': errors?.state_id }"
                                        :customClass="{ 'is-invalid': errors?.state_id }" :initialize="tender.state_id"
                                        id="state_id" label="state_name" placeholder="Select State" :data="states"
                                        @input="state => tender.state_id = state">
                                    </search>
                                    <span v-if="errors?.state_id" class="invalid-feedback">{{ errors?.state_id[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Bid / RFP Number <span class="text-danger">*</span></label>
                                    <input type="text" class="form-control form-control-sm"
                                        placeholder="Bid / RFP Number" :class="{ 'is-invalid': errors?.tender_no }"
                                        v-model="tender.tender_no" />
                                    <span v-if="errors?.tender_no" class="invalid-feedback">{{ errors?.tender_no[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Bid Title <span class="text-danger">*</span></label>
                                    <input type="text" class="form-control form-control-sm" placeholder="Bid Title"
                                        :class="{ 'is-invalid': errors?.title }" v-model="tender.title" />
                                    <span v-if="errors?.title" class="invalid-feedback">{{ errors?.title[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Issuing Agency <span class="text-danger">*</span></label>
                                    <search :class="{ 'is-invalid': errors?.federal_agency_id }"
                                        :customClass="{ 'is-invalid': errors?.federal_agency_id }"
                                        :initialize="tender.federal_agency_id" id="federal_agency_id"
                                        label="agency_name" placeholder="Select Agency" :data="agencies"
                                        @input="agency => tender.federal_agency_id = agency"
                                        @updateAgencies="updateAgencies">
                                    </search>
                                    <span class="invalid-feedback" v-if="errors?.federal_agency_id?.length">{{
                                        errors?.federal_agency_id[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Notice <span class="text-danger">*</span></label>
                                    <select class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors?.federal_notice_id }"
                                        v-model="tender.federal_notice_id">
                                        <option value="">Select Notice</option>
                                        <option v-for="notice, notice_key in notices" :key="notice_key"
                                            :value="notice.federal_notice_id">{{ notice.notice_name }}</option>
                                    </select>
                                    <span class="invalid-feedback" v-if="errors?.federal_notice_id?.length">{{
                                        errors?.federal_notice_id[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Opening Date <span class="text-danger">*</span></label>
                                    <input type="date" class="form-control form-control-sm" placeholder="Opening Date"
                                        :class="{ 'is-invalid': errors?.opening_date }" v-model="tender.opening_date" />
                                    <span v-if="errors?.opening_date" class="invalid-feedback">{{
                                        errors?.opening_date[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Due Date <span class="text-danger">*</span></label>
                                    <input type="date" class="form-control form-control-sm" placeholder="Due Date"
                                        :class="{ 'is-invalid': errors.expiry_date }" v-model="tender.expiry_date" />
                                    <span v-if="errors?.expiry_date" class="invalid-feedback">{{ errors?.expiry_date[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Contract Type </label>
                                    <search :class="{ 'is-invalid': errors?.contract_type_id }"
                                        :customClass="{ 'is-invalid': errors?.contract_type_id }" :initialize="tender.contract_type_id"
                                        id="contract_type_id" label="contract_type" placeholder="Select Contract Type" :data="contract_types"
                                        @input="set_aside => tender.contract_type_id = set_aside">
                                    </search>
                                    <span v-if="errors?.contract_type_id" class="invalid-feedback">{{ errors?.contract_type_id[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Naics <span class="text-danger">*</span></label>
                                    <search :class="{ 'is-invalid': errors?.naics_id }"
                                        :customClass="{ 'is-invalid': errors?.naics_id }" :initialize="tender.naics_id"
                                        id="naics_id" label="name" placeholder="Select Naics" :data="naicses"
                                        @input="naics => tender.naics_id = naics">
                                    </search>
                                    <span v-if="errors?.naics_id" class="invalid-feedback">{{ errors?.naics_id[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Set Aside Status <span class="text-danger">*</span></label>
                                    <search :class="{ 'is-invalid': errors?.set_aside_id }"
                                        :customClass="{ 'is-invalid': errors?.set_aside_id }" :initialize="tender.set_aside_id"
                                        id="set_aside_id" label="set_aside_name" placeholder="Select Set Aside Status" :data="set_asides"
                                        @input="set_aside => tender.set_aside_id = set_aside">
                                    </search>
                                    <span v-if="errors?.set_aside_id" class="invalid-feedback">{{ errors?.set_aside_id[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Award Types </label>
                                    <select class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors?.award_type_id }"
                                        v-model="tender.award_type_id">
                                        <option value="">Select Award Type</option>
                                        <option v-for="award_type, award_type_key in award_types" :key="award_type_key"
                                            :value="award_type.award_type_id">{{ award_type.award_type_name }}</option>
                                    </select>
                                    <span class="invalid-feedback" v-if="errors?.award_type_id?.length">{{
                                        errors?.award_type_id[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Pscs <span class="text-danger">*</span></label>
                                    <search :class="{ 'is-invalid': errors?.psc_id }"
                                        :customClass="{ 'is-invalid': errors?.psc_id }" :initialize="tender.psc_id"
                                        id="psc_id" label="name" placeholder="Select Psc" :data="pscs"
                                        @input="psc => tender.psc_id = psc">
                                    </search>
                                    <span v-if="errors?.psc_id" class="invalid-feedback">{{ errors?.psc_id[0]
                                        }}</span>
                                </div>
                            </div>
                            
                            <div class="col-sm-12 margin_top">Contracting Office Address</div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label>City </label>
                                    <input type="text" class="form-control form-control-sm" placeholder="City"
                                        :class="{ 'is-invalid': errors?.federal_office_address?.city }"
                                        v-model="tender.federal_office_address.city" />
                                    <span v-if="errors.federal_office_address?.city" class="invalid-feedback">{{
                                        errors?.federal_office_address?.city[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label>State </label>
                                    <input type="text" class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors?.federal_office_address?.state }"
                                        placeholder="State" v-model="tender.federal_office_address.state" />
                                    <span v-if="errors?.federal_office_address?.state" class="invalid-feedback">{{
                                        errors?.federal_office_address?.state[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label>Country </label>
                                    <input type="text" class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors?.federal_office_address?.country }"
                                        placeholder="Country" v-model="tender.federal_office_address.country" />
                                    <span v-if="errors?.federal_office_address?.country" class="invalid-feedback">{{
                                        errors?.federal_office_address?.country[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Bid Posting Link </label>
                                    <input type="text" class="form-control form-control-sm"
                                        placeholder="Bid Posting Link" :class="{ 'is-invalid': errors.tender_url }"
                                        v-model="tender.tender_url" />
                                    <span v-if="errors.tender_url" class="invalid-feedback">{{ errors.tender_url[0]
                                        }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Single Download Fee </label>
                                    <input type="text" class="form-control form-control-sm"
                                        placeholder="Single Download Fee" :class="{ 'is-invalid': errors.fees }"
                                        v-model="tender.fees" />
                                    <span v-if="errors?.fees" class="invalid-feedback">{{ errors?.fees[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-4 margin_top">
                                <div class="form-group">
                                    <label>Category </label>
                                    <search :class="{ 'is-invalid': errors?.category_id }"
                                        :customClass="{ 'is-invalid': errors?.category_id }"
                                        :initialize="tender.category_id" id="category_id" label="category_name"
                                        placeholder="Select Category" :data="categories"
                                        @input="category => tender.category_id = category">
                                    </search>
                                    <span class="invalid-feedback" v-if="errors?.category_id?.length">{{
                                        errors?.category_id[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-12 margin_top">
                                <div class="form-group">
                                    <label>Bid Description </label>
                                    <ckeditor :editor="editor" v-model="tender.description"></ckeditor>
                                </div>
                            </div>
                            <div class="col-sm-12 margin_top">Primary Address</div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Title </label>
                                    <input type="text" class="form-control form-control-sm" placeholder="Title"
                                        :class="{ 'is-invalid': errors.primary_address?.title }"
                                        v-model="tender.primary_address.title" />
                                    <span class="invalid-feedback" v-if="errors?.primary_address?.title?.length">{{
                                        errors?.primary_address?.title[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Full Name </label>
                                    <input type="text" class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors.primary_address?.full_name }"
                                        placeholder="Full Name" v-model="tender.primary_address.full_name" />
                                    <span class="invalid-feedback" v-if="errors?.primary_address?.full_name?.length">{{
                                        errors?.primary_address?.full_name[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Email </label>
                                    <input type="text" class="form-control form-control-sm" placeholder="Email"
                                        :class="{ 'is-invalid': errors.primary_address?.email }"
                                        v-model="tender.primary_address.email" />
                                    <span class="invalid-feedback" v-if="errors?.primary_address?.email?.length">{{
                                        errors?.primary_address?.email[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Phone </label>
                                    <input type="text" class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors.primary_address?.phone }" placeholder="Phone"
                                        v-model="tender.primary_address.phone" />
                                    <span class="invalid-feedback" v-if="errors?.primary_address?.phone?.length">{{
                                        errors?.primary_address?.phone[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-12 margin_top">Secodary Address</div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Title </label>
                                    <input type="text" class="form-control form-control-sm" placeholder="Title"
                                        :class="{ 'is-invalid': errors.secondary_address?.title }"
                                        v-model="tender.secondary_address.title" />
                                    <span class="invalid-feedback" v-if="errors?.secondary_address?.title?.length">{{
                                        errors?.secondary_address?.title[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Full Name </label>
                                    <input type="text" class="form-control form-control-sm" placeholder="Full Name"
                                        :class="{ 'is-invalid': errors.secondary_address?.full_name }"
                                        v-model="tender.secondary_address.full_name" />
                                    <span class="invalid-feedback"
                                        v-if="errors?.secondary_address?.full_name?.length">{{
                                            errors?.secondary_address?.full_name[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Email </label>
                                    <input type="text" class="form-control form-control-sm" placeholder="Email"
                                        :class="{ 'is-invalid': errors.secondary_address?.email }"
                                        v-model="tender.secondary_address.email" />
                                    <span class="invalid-feedback" v-if="errors?.secondary_address?.email?.length">{{
                                        errors?.secondary_address?.email[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Phone </label>
                                    <input type="text" class="form-control form-control-sm" placeholder="Phone"
                                        :class="{ 'is-invalid': errors.secondary_address?.phone }"
                                        v-model="tender.secondary_address.phone" />
                                    <span class="invalid-feedback" v-if="errors?.secondary_address?.phone?.length">{{
                                        errors?.secondary_address?.phone[0] }}</span>
                                </div>
                            </div>
                            <div class="col-sm-3 margin_top">
                                <div class="form-group">
                                    <label>Attachments </label>
                                    <input type="file" class="form-control form-control-sm"
                                        :class="{ 'is-invalid': errors.attachments }" multiple
                                        @change="handleFileUpload($event)" />
                                    <span class="invalid-feedback" v-if="errors?.attachments?.length">{{
                                        errors?.attachments[0] }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="card-footer text-end">
                            <button class="btn btn-warning me-2">Discard</button>
                            <button class="btn btn-primary " type="submit">
                                <span v-if="status">Submit </span>
                                <span v-else>Update </span>
                            </button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
import search from "@/components/Search.vue";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
export default {
    components: {
        // Pagination,
        search,
    },

    data() {
        return {
            status: true,
            tender: {
                federal_tender_id: "",
                region: "federal",
                country_id: "10",
                state_id: "",
                tender_no: "",
                title: "",
                description: "",
                federal_agency_id: "",
                federal_notice_id: "",
                opening_date: "",
                expiry_date: "",
                award_type_id:'',
                naics_id:'',
                contract_type_id:'',
                psc_id:'',
                tender_url: "",
                fees: "",
                category_id: "",
                // place_of_performance: {
                //     city_name: '',
                //     state_name: '',
                //     country_name: ''
                // },
                federal_office_address: {
                    city: "",
                    state: "",
                    country: "",
                },
                primary_address: {
                    title: "",
                    full_name: "",
                    email: "",
                    phone: "",
                },
                secondary_address: {
                    title: "",
                    full_name: "",
                    email: "",
                    phone: "",
                },
                attachments: [],
            },
            countries: [],
            states: [],
            notices: [],
            categories: [],
            agencies: [],
            award_types:[],
            naicses:[],
            set_asides:[],
            contract_types:[],
            errors: [],
            pscs:[],
            editor: ClassicEditor,
        };
    },
    // mounted() {
    //     this.getCountries()
    //     if (this.tender.country_id) {
    //         this.getStates()
    //     }
    // },
    beforeRouteEnter(to, from, next) {
        next((vm) => {
            vm.getCountries();
            if (vm.tender.country_id) {
                vm.getStates();
            }
            if (to.name == "EditFederalTender") {
                console.log(to.params)                    
                vm.status = false;
                let uri = { uri: "getFederalTender", data: { federal_tender_id: to.params.federal_tender_id } };
                vm.$store
                    .dispatch("post", uri)
                    .then(function (response) {
                        console.log("response-----", response.data.data);
                        vm.tender = response.data.data;
                        console.log("tender--",vm.tender)
                    })
                    .catch(function (error) {
                        vm.errors = error.response.data.errors;
                        vm.$store.dispatch("error", error.response.data.message);
                    });
            }
        });
    },
    methods: {
        handleFileUpload(event) {
            this.tender.attachments = event.target.files;
        },
        submitForm() {
            let vm = this;
            if (vm.status) {
                vm.addTender();
            } else {
                vm.updateTender();
            }
        },
        addTender() {
            let vm = this;
            let loader = vm.$loading.show();
            const formData = new FormData();
            for (var i = 0; i < vm.tender?.attachments?.length; i++) {
                formData.append("attachments[" + i + "]", vm.tender.attachments[i]);
            }
            formData.append("country_id", vm.tender.country_id);
            formData.append("state_id", vm.tender.state_id);
            formData.append("tender_no", vm.tender.tender_no);
            formData.append("title", vm.tender.title);
            formData.append("federal_agency_id", vm.tender.federal_agency_id || '');
            formData.append("federal_notice_id", vm.tender.federal_notice_id || '');
            formData.append("opening_date", vm.tender.opening_date);
            formData.append("expiry_date", vm.tender.expiry_date || '');
            formData.append("place_of_performance", JSON.stringify(vm.tender.place_of_performance || ''));
            formData.append("federal_office_address", JSON.stringify(vm.tender.federal_office_address || ''));
            formData.append("tender_url", vm.tender.tender_url);
            formData.append("fees", vm.tender.fees || '');
            formData.append("category_id", vm.tender.category_id || '');
            formData.append("description", vm.tender.description || '');
            formData.append("primary_address", JSON.stringify(vm.tender.primary_address || ''));
            formData.append("secondary_address", JSON.stringify(vm.tender.secondary_address || ''));
            formData.append("naics_id", vm.tender.naics_id)
            formData.append("psc_id", vm.tender.psc_id)
            formData.append("contract_type_id", vm.tender.contract_type_id)
            formData.append("set_aside_id", vm.tender.set_aside_id)
            formData.append("award_type_id", vm.tender.award_type_id)

            vm.$store
                .dispatch("multipart_formdata", { uri: "addFederalTender", data: formData })
                .then(() => {
                    loader.hide();
                    vm.discard();
                    vm.$router.push("/federal_tenders");
                })
                .catch(function (error) {
                    loader.hide();
                    vm.errors = error.response.data.errors;
                    vm.$store.dispatch("error", error.response.data.message);
                });
        },
        updateTender() {
            let vm = this;
            let loader = vm.$loading.show();
            const formData = new FormData();
            for (var i = 0; i < vm.tender?.attachments?.length; i++) {
                formData.append("attachments[" + i + "]", vm.tender.attachments[i]);
            }
            formData.append("federal_tender_id", vm.tender.federal_tender_id);
            formData.append("country_id", vm.tender.country_id);
            formData.append("state_id", vm.tender.state_id);
            formData.append("tender_no", vm.tender.tender_no);
            formData.append("title", vm.tender.title);
            formData.append("federal_agency_id", vm.tender.federal_agency_id || '');
            formData.append("federal_notice_id", vm.tender.federal_notice_id || '');
            formData.append("opening_date", vm.tender.opening_date);
            formData.append("expiry_date", vm.tender.expiry_date || '');
            formData.append("place_of_performance", JSON.stringify(vm.tender.place_of_performance || ''));
            formData.append("federal_office_address", JSON.stringify(vm.tender.federal_office_address || ''));
            formData.append("tender_url", vm.tender.tender_url);
            formData.append("fees", vm.tender.fees || '');
            formData.append("category_id", vm.tender.category_id || '');
            formData.append("description", vm.tender.description || '');
            formData.append("primary_address", JSON.stringify(vm.tender.primary_address || ''));
            formData.append("secondary_address", JSON.stringify(vm.tender.secondary_address || ''));
            formData.append("naics_id", vm.tender.naics_id)
            formData.append("psc_id", vm.tender.psc_id)
            formData.append("contract_type_id", vm.tender.contract_type_id)
            formData.append("set_aside_id", vm.tender.set_aside_id)
            formData.append("award_type_id", vm.tender.award_type_id)
            vm.$store
                .dispatch("multipart_formdata", { uri: "updateTenderFederal", data: formData })
                .then(() => {
                    loader.hide();
                    vm.discard();
                    vm.$router.push("/federal_tenders");
                })
                .catch(function (error) {
                    loader.hide();
                    vm.errors = error.response.data.errors;
                    vm.$store.dispatch("error", error.response.data.message);
                });
        },
        discard() {
            (this.tender.region = "federal"),
                (this.tender.country_id = "10"),
                (this.tender.state_id = ""),
                (this.tender.tender_no = ""),
                (this.tender.title = ""),
                (this.tender.description = ""),
                (this.tender.federal_agency_id = ""),
                (this.tender.federal_notice_id = ""),
                (this.tender.opening_date = ""),
                (this.tender.expiry_date = ""),
                (this.tender.tender_url = ""),
                (this.tender.fees = ""),
                (this.tender.category_id = ""),
                (this.tender.place_of_performance = {
                    city_name: "",
                    state_name: "",
                    country_name: "",
                }),
                (this.tender.federal_office_address = {
                    city: "",
                    state: "",
                    country: "",
                }),
                (this.tender.primary_address = {
                    title: "",
                    full_name: "",
                    email: "",
                    phone: "",
                }),
                (this.tender.secondary_address = {
                    title: "",
                    full_name: "",
                    email: "",
                    phone: "",
                }),
                (this.tender.attachments = []);
        },

        handleCountryChange(country) {
            this.tender.country_id = country;
            if (typeof this.tender.country_id === "number") {
                this.getStates();
            }
        },
        getCountries() {
            let vm = this;
            let loader = vm.$loading.show();
            vm.$store
                .dispatch("post", { uri: "getCountries" })
                .then((response) => {
                    loader.hide();
                    vm.countries = response.data;
                    vm.getFederalNotices();
                })
                .catch(function (error) {
                    loader.hide();
                    vm.errors = error.response.data.errors;
                    vm.$store.dispatch("error", error.response.data.message);
                });
        },

        getStates() {
            let vm = this;
            let loader = vm.$loading.show();
            vm.$store
                .dispatch("post", { uri: "getStates", data: vm.tender })
                .then((response) => {
                    loader.hide();
                    vm.states = response.data;
                })
                .catch(function (error) {
                    loader.hide();
                    vm.errors = error.response.data.errors;
                    vm.$store.dispatch("error", error.response.data.message);
                });
        },

        getFederalNotices() {
            let vm = this;
            let loader = vm.$loading.show();
            vm.$store
                .dispatch("post", { uri: "getFederalNotices", data: { meta: { region_id: 1 } } })
                .then((response) => {
                    loader.hide();
                    vm.notices = response.data;
                    vm.getCategories();
                })
                .catch(function (error) {
                    loader.hide();
                    vm.errors = error.response.data.errors;
                    vm.$store.dispatch("error", error.response.data.message);
                });
        },

        getCategories() {
            let vm = this;
            let loader = vm.$loading.show();
            vm.$store
                .dispatch("post", { uri: "getCategories" })
                .then((response) => {
                    loader.hide();
                    vm.categories = response.data;
                    vm.getFederalAgencies();
                })
                .catch(function (error) {
                    loader.hide();
                    vm.errors = error.response.data.errors;
                    vm.$store.dispatch("error", error.response.data.message);
                });
        },

        getFederalAgencies() {
            let vm = this;
            vm.$store
                .dispatch("post", { uri: "getFederalAgencies" })
                .then((response) => {
                    console.log("Agenciyy---",response.data)
                    vm.agencies = response.data
                    vm.getAwardTypes()
                })
                .catch(function (error) {
                    console.log(error);
                });
        },

        getAwardTypes() {
            let vm = this;
            vm.$store
                .dispatch("post", { uri: "getAwardTypes" })
                .then((response) => {
                    vm.award_types = response.data;
                    vm.getPrimaryNaics()
                })
                .catch(function (error) {
                    console.log(error);
                });
        },

        getPrimaryNaics() {
            let vm = this;
            vm.$store
                .dispatch("post", { uri: "getPrimaryNaics" })
                .then((response) => {
                    vm.naicses = response.data.data
                    vm.getSetAsides()
                })
                .catch(function (error) {
                    console.log(error);
                });
        },

        getSetAsides() {
            let vm = this;
            vm.$store
                .dispatch("post", { uri: "getSetAsides" })
                .then((response) => {
                    vm.set_asides = response.data
                    vm.getContractTypes()
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        getContractTypes() {
            let vm = this;
            vm.$store
                .dispatch("post", { uri: "getContractTypes" })
                .then((response) => {
                    vm.contract_types = response.data
                    vm.getPrimaryPscs()
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        getPrimaryPscs() {
            let vm = this;
            vm.$store
                .dispatch("post", { uri: "getPrimaryPscs" })
                .then((response) => {
                console.log("RE--",response.data.data)
                    vm.pscs = response.data.data
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
    },
};
</script>
